@import '../setup/vars';
@import '../setup/mixins';

.welcome-section {
  color: white;
  text-align: center;
  height: $top-section__height;
  width: 100%;
  .primary-button {
    margin: 120px auto 0 auto; }
  @include element('shadow') {
    background-color: rgba(0, 0, 0, 0.20);
    width: 100%;
    height: 100%;
    .slick-prev, .slick-next {
      top: $top-section__height / 2;
      width: 200px;
      height: $top-section__height;
      position: absolute;
      &::before {
        transition: opacity 0.5s ease;
        font-size: 40px; }
      @media screen and (max-width: 1024px) {
        width: 90px; }
      @media screen and (max-width: 425px) {
        width: 50px;
        top: $top-section__height / 1.8;
        height: $top-section__height - ($top-section__height - ($top-section__height / 1.8)); } }
    .slick-prev {
      left: 0;
      box-shadow: inset 20px 0 20px 0  rgba(0, 0, 0, 0.30);
      @media screen and (max-width: 425px) {
        box-shadow: none; } }
    .slick-next {
      right: 0;
      box-shadow: inset -20px 0 20px 0  rgba(0, 0, 0, 0.30);
      @media screen and (max-width: 425px) {
        box-shadow: none; } } }
  @include element('wrapper') {
    padding-top: 300px;
    margin: 0 auto;
    display: flex;
    flex-flow: column nowrap;
    width: 900px;
    @media screen and (max-width: 1024px) {
      width: 600px; }
    @media screen and (max-width: 768px) {
      width: 400px; }
    @media screen and (max-width: 425px) {
      width: 300px; } }
  @include element('primary-header') {
    margin: 0;
    font-size: 72px;
    @media screen and (max-width: 768px) {
      font-size: $font__size--1;
      text-decoration: underline; } }
  @include element('secondary-header') {
    margin: 0;
    @media screen and (max-width: 768px) {
      font-size: $font__size--5; } } }
